@import '../../../base';
@import '../forms';

.multi-drop-v3 {
  $multi-input-padding-left: 45px;
  $multi-selected-padding: 8px;

  .dropdown-container {
    .title-header {
      padding-left: $multi-input-padding-left;
    }
  }

  .selected-container {
    display: flex;
    .icon-container {
      @extend %img-container;
    }
    
    .selected-text {
      padding-left: $multi-input-padding-left - 1px;
    }

    .selected-items-container {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      padding-right: 45px;
  
      .selected-text-multi {
        height: 36px;
        border-radius: 12px;
        display: inline-block;
        position: relative;
        padding: 0;
        overflow: inherit;
        flex-grow: 1;
        padding-top: 5px;
        width: auto;

        input {
          height: auto;
          transform: translateY(5px);
          text-overflow: ellipsis;

          &::placeholder {
            font-weight: $font-weight-normal;
            color: $color-input-title-inactive;
            text-overflow: ellipsis;
          }
        }
      }
  
      .selected-item-container {
        height: 24px;
        border-radius: 12px;
        padding: 0 $multi-selected-padding;
        margin: 4px;
        border: 1px solid $color-input-outline;
        display:inline-block;
  
        .selected-item {
          display:flex;
          align-items: center;
          height: 100%;
          
          p {
            margin: 0;
            font-weight: $font-weight-bold;
            font-size: $font-size-tiny;
            color: $color-input-text;
          }
  
          .selected-item-destroy {
            display: flex;
            align-items: center;
  
            img {
              margin-left: $multi-selected-padding;
              width: $multi-selected-padding;
              height: $multi-selected-padding;
              filter: $color-filter-input-cross;
            }
          }
  
          .selected-item-destroy:hover {
            filter: $color-filter-body;
          }
        }
      }
    }
  }
}
