/**
 * DropDown.css
 * CSS for custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
 @import '../../../base';
 @import '../forms';

.input-wrapper-main-v3 {
  width: 100%;
  margin: 10px 0;
  padding: 0 5px;
  position: relative;
  // min-height: $input-height;

  @media screen and (max-width: 576px) {
    margin-bottom: 5px;
  }

  .input-wrapper-desc {
    min-height: 18px;
    font-weight: $font-weight-bold;
    font-size: $font-size-tiny;

    color: $color-title;
    margin-left: 2px;

    @media screen and (max-width: 576px) {
        min-height: 0;
    }
  }

  .input-wrapper-error-text {
    margin-top: 2px;
    display: flex;
    align-items: center;
    p {
      color: white;
      background-color: $color-error-alt;
      font-size: $font-size-tiny;
      font-weight: $font-weight-semibold;
      border-radius: $input-border-radius;
      padding: 5px 10px;
    }
  }

  .extra-info-container {
    margin-top: 5px;
    p {
      text-align: left;
      font-weight: $font-weight-normal;
      font-size: $font-size-tiny;
      color: $color-other-desc;
    }

    b {
      font-weight: $font-weight-bold;
    }

    .green {
      color: $color-hover;
    }
  }

  .error {
    @extend %error-style;
  }
}
