@import '../../base';

$input-border-radius: 12px;
$input-height: 48px;
$input-padding: 15px;
$input-padding-inner: $input-padding - 1px;

%input-container {
  width: 100%;
  border: 1px solid $color-input-outline;
  box-sizing: border-box;
  border-radius: $input-border-radius;
  padding: 0;

  transition: border 0.3s ease;
}

%input-container-focus {
  // box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
  border: 2px solid $color-hover;
}

%title-header {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 $input-padding;
  transform: translateY($input-padding);

  font-weight: $font-weight-normal;
  font-size: $font-size-tiny;
  color: $color-input-title-inactive;

  transition: color 0.3s ease, transform 0.3s ease, font-size 0.3s ease;
}

%title-header-focus {
  font-size: $font-size-extra-tiny !important;
  color: $color-hover !important;
  transform: translateY(8px) !important;
}

%input-field-container {
  // position: absolute;
  // height: 100%;
  // width: 100%;
  // top: 0;
  // left: 0;
  padding: 0 $input-padding-inner $input-padding-inner * 0.5 $input-padding-inner;
}

%input-field {
  // height: calc(100% - #{$input-padding-inner});
  &:focus {
    box-shadow: none;
  }
  padding: 1px;
  flex: 1;
  width: 100%;
  max-height: 18px;
  transform: translateY($input-padding-inner + 8px);
  border: none;

  background-color: transparent;
  font-weight: $font-weight-bold;
  font-size: $font-size-tiny;

  color: $color-input-text;

  &::placeholder {
    font-weight: $font-weight-semibold;
    font-size: $font-size-tiny;
    color: $color-input-title-inactive;
    text-overflow: ellipsis;
  }

  &:focus {
    outline: none;
  }
}


%error-style {
  transition: all 0.3s ease;
  border: 2px solid $color-error-alt !important;
}

%img-container {
  display: flex;
  align-items: center;
  img {
    margin: 0 8px 0 15px;
    filter: $color-filter-input-title-inactive;
    width: 15px;
    height: 15px;
  }
}

%drop-arrow-container {
  position: absolute;
  display: flex;
  justify-content: center;
  height: 100%;
  top: 0;
  right: 22px;
  img {
    width: 10px;
    filter: $color-filter-hover;
    margin-top: 0px;
  }

  .angle-up {
    transform: rotate(-180deg);
  }
  cursor: pointer;
}

@keyframes animate-open {
  0% { z-index: 1000; }
  100% { z-index: 1000; }
}

@keyframes animate-close {
  0% { z-index: 1000; }
  100% { z-index: 0; }
}

.drop-open {
  animation: animate-open 3s infinite;
}

.drop-close {
  animation: animate-close 3s linear;
}

@keyframes animate-z-visible-v3 {
  0% { 
    z-index: -999;
  }
  1% {
    z-index: 1029;
  }
  100% { 
    z-index: 1029;
  }
}

@keyframes animate-z-invisible-v3 {
  0% { 
    z-index: 1029;
  }
  99% {
    z-index: 1029;
  }
  100% { 
    z-index: -999;
  }
}

@keyframes animate-visible {
  0% { 
    transform: scale(0);
    transform-origin: top left;
  }
  100% { 
    transform: scale(1);
    transform-origin: top left;
  }
}

@keyframes animate-invisible {
  0% { 
    transform: scale(1);
    transform-origin: top left;
  }
  100% { 
    transform: scale(0);
    transform-origin: top left;
  }
}

%close-init {
  transform: scale(0);
  transform-origin: top left;
  z-index: -999;
}

.no-internal-desc {
  input {
    max-height: 32px !important;
    transform: translateY(7px) !important;
  }
}