/**
 * DropDown.css
 * CSS for custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
 @import '../../../base';
 @import '../forms';

.dropdown-container-v3 {
  position: relative;

  &.no-internal-desc {
    .selected-text {
      input {
        transform: translateY(8px) !important;
      }
    }

    .list-item {
      p {
        transform: translateY(12px) !important;
      }
    }
  }

  .dropdown-container {
    @extend %input-container;
    min-height: $input-height;
    background-color: white;

    .title-header {
      @extend %title-header;
    }
  
    .focused-title {
      @extend %title-header-focus;
    }
  }
  
  .dropdown-container-closed {
    padding: 1px;
  }
  
  .dropdown-container:hover,
  .dropdown-container-opened {
    padding: 0;
    @extend %input-container-focus;
    .title-header {
      @extend %title-header-focus;
    }
  }
  
  .error {
    @extend %error-style;
  }
  
  .text-style {
    @extend %input-field-container;

    p,
    input {
      @extend %input-field;
    }
  }
  
  .selected-container {
    z-index: 1;
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    
    .selected-text {
      @extend %input-field-container;

      input {
        @extend %input-field;
      }
      input[type="text"]:disabled {
        color: $color-input-text;
        -webkit-text-fill-color: #000000;
      }
    }

    &:focus,
    .selected-text input:focus {
      outline: none;
    }
    
    .selected-text input::placeholder {
      color: $color-line;
    }
    
    .selected-arrow {
      @extend %drop-arrow-container;
    }
  }
  
  
  .list-container {
    width: 100%;
    list-style-type: none;
    // background-color: white;
    border-bottom-left-radius: $input-border-radius;
    border-bottom-right-radius: $input-border-radius;

    .list-item {
      display: flex;
      position: relative;
      border-radius: 12px;
    }
    
    .list-item:focus {
      outline: none;
    }
    
    .list-item:focus,
    .list-item:hover {
      background: $color-form;
    }
    
    .list-item p {
      @extend %input-field;
      position: relative;
      display: flex;
    }
  }
  
  .list-closed {
    margin-top: 0;
    max-height: 0;
    transition: min-height 0.3s ease;
  }
  
  .list-opened {
    margin-top: $input-height;
    transition: min-height 0.3s ease;
  }
}

